<template>
  <div class="main">
    <div class="header">
      {{title}}
      <span>
        <img src="@/assets/icon_Refresh.png" />
      </span>
    </div>

    <div class="content">
      <div class="map">
        <div class="seat" v-for="(obj, index) in list" :key="index"
          :style="{ width: obj.xy[0] + 'px', height: obj.xy[1] + 'px', left: obj.xy[2] + 'px', top: obj.xy[3] + 'px' }"
          :class="obj.status" @click="handleShowMsg(index)">
          <div class="icon"></div>
          <div class="alert"></div>
          <div class="bg"></div>
        </div>
      <div class="message" ref="message" v-show="isShowMsg">
        <div class="title">
          <img src='@/assets/icon_time.png' />
          <span>2020-5-18</span>
          <span>星期五</span>
          <span>11:22AM</span>
        </div>
        <div>
          <img src="@/assets/icon_box.png" />
          Smart Box 1758
          <span class="status doing">运行</span>
        </div>
        <div class="line"></div>
        <div class="device">
          关联设备
          <span><img src="@/assets/icon_printer.png" /></span>
          <span><img src="@/assets/Notebook_computer.png" /></span>
          <span><img src="@/assets/Desk_lamp.png" /></span>
          <span><img src="@/assets/icon_Worktable.png" /></span>
        </div>
        <div class="foot">
          <div>
            <img src="@/assets/icon_switch.png" />
          </div>
        </div>
      </div>
      </div>
    </div>
  </div>
</template>

<script>
import { seatList } from '@/JSON'
export default {
  data() {
    return {
      list: [],
      isShowMsg: false,
      title: '北京总部A座·1楼·南一区'
    }
  },
  mounted() {
    this.$el.addEventListener('click', event => {
      console.log(event.path)
      if (!event.path.some(ele => {
        if (ele.className && (ele.className.indexOf('seat') !== -1 || ele.className.indexOf('message') !== -1)) {
          return true
        }
      })) {
        this.isShowMsg = false
      }
    })
    this.$root.$on('change', this.init)
    this.init()
  },
  methods: {
    // 生成随机状态
    init(arg) {
      if (arg instanceof Array) {
        this.title = arg.slice(0, arg.length - 1).join('·')
      }
      this.list = seatList().map(ele => {
        let a = Math.round(Math.random() * 10) % 3
        switch(a) {
          case 1:
            ele.status = 'warning'
            break;
          case 2:
            ele.status = 'offline'
            break;
          default:
            ele.status = ''
        }
        return ele
      })
      if (!this.list.length) {
        this.init(arg)
      } else {
        this.list = [].concat(this.list)
      }
    },
    // 显示信息框
    handleShowMsg(index) {
      this.isShowMsg = true
      console.log('handleShowMsg', this.isShowMsg)
      const dom = this.$refs.message
      dom.style.left = (this.list[index].xy[2] - 118) + 'px'
      dom.style.top = (this.list[index].xy[3] - 250) + 'px'
    }
  }

}
</script>

<style lang="less" scoped>
.main {
  display: flex;
  flex-direction: column;
  > .header {
    padding: 48px 0 25px;
    background-color: #212633;
    width: 100%;
    color: #FFF;
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    > span {
      float: right;
      margin-right: 20px;
      cursor: pointer;
    }
}
}
.content {
  flex-grow: 2;
  .map {
    zoom: 0.8;
    margin: auto;
    width: 1038px;
    height: 924px;
    background-image: url('../../assets/Linear_map.png');
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: top left;
    position: relative;
    .seat {
      // width: 63px;
      // height: 72px;
      // left: 335px;
      // top: 398px;
      position: absolute;
      > div.bg {
        // 背景
        width: 100%;
        height: 100%;
        opacity: 0.3;
        background: #0096F8;
        position: absolute;
        left: 0;
        top: 0;
      }
      div.icon, div.alert {
        width: 15px;
        height: 15px;
        position: absolute;
        background-repeat: no-repeat;
        background-size: contain;
      }
      > div.icon {
        // 左下角
        left: 5px;
        bottom: 5px;
        background-image: url('../../assets/icon_device_management.png');
      }
      > div.alert {
        // 右上角
        display: none;
        top: 5px;
        right: 5px;
        background-image: url('../../assets/icon_warning.png');
      }
      &.active {
        border: 2px solid #7D8AA6;
      }
      &.warning {
        div.bg {
          background-color: #E64646;
        }
        div.icon {
          background-image: url('../../assets/icon_equipment.png');
        }
        div.alert {
          display: block;
        }
      }
      &.offline {
        div.bg {
          background-color: #7D8AA6;
        }
        div.icon {
          background-image: url('../../assets/icon_equipment2.png');
        }
        div.alert {
          display: none;
        }
      }
    }
  }
}
.message {
  top: 100px;
  left: 200px;
  position: absolute;
  width: 286px;
  background-color: #4e5773;
  border-radius: 3px;
  font-size: 14px;
    color: #8F9EBF;
  padding: 8px 0;
  line-height: 36px;
  &:after {
    content: "";
    width: 0;
    height: 0;
    border-top: 8px solid #4e5773;
    border-bottom: none;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    position: absolute;
    left: 50%;
    bottom: -8px;
    margin-left: -4px;
  }
  img {
    vertical-align: middle;
    margin-right: 14px;
  }
  > .title {
    padding: 0 16px;
    > span {
      padding-right: 12px;
      &:last-child {
        float: right;
      }
    }
    & + div {
      padding: 0 16px 10px;
      color: #fff;
      > span {
        display: inline-block;
        float: right;
      }
    }
  }
  > .line {
    height: 1px;
    background-image: url('../../assets/Line2.png');
    background-repeat: no-repeat;
    background-size: contain;
  }
  > .device {
    padding: 16px;
    > span {
      background: #5F6A8C;
      width: 32px;
      height: 32px;
      line-height: 32px;
      display: inline-block;
      border-radius: 50%;
      border: 2px solid #4e5773;
      text-align: center;
      img {
        margin: 0;
      }
      &:first-child {
        margin-left: 10px;
      }
    }
  }
  > .foot {
    text-align: center;
    padding: 10px;
    > div {
      width: 56px;
      height: 56px;
      line-height: 56px;
      border-radius: 50%;
      background: #5B678C;
      margin: auto;
      img {
        margin: 0;
      }
    }
  }
}
</style>